.profile {
  width: 330px;
  /* height: 250px; */
  padding-top: 35px;
  overflow: hidden;
  margin-left: auto;
  margin-right: auto;
  background-color: #ffff;
  border-radius: 5px;
  box-shadow: 0px 1px 3px 0px rgba(191, 205, 222, 0.75);
  margin-bottom: 25px;
}

.description {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 35px;
}

.avatar {
  display: block;
  box-shadow: inset 0px 1px 2px 0px rgba(0, 0, 0, 0.15);
  border-radius: 70px;
}

.name {
  font-weight: 700;
  font-size: 18px;
  padding-top: 20px;
  padding-bottom: 10px;
}

.tag {
  padding-bottom: 10px;
  color: #8a8282;
}

.location {
  color: #8a8282;
}

.stats {
  display: flex;
}

.el {
  width: 33.333333333%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #f3f6f9;
  height: 100px;
  box-shadow: inset 0px 1px 2px 0px rgba(0, 0, 0, 0.15);
}

.label {
  color: #8a8282;
  padding-bottom: 10px;
}

.quantity {
  font-weight: 700;
}
